import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Button, Container, Row, Col, Card, Tab, Nav } from "react-bootstrap";
import Api from "./Api";
import HashHighlighter from './HashHighlighter';
import CardAside from "../CommonComponents/CardAside";
import Download from './DownloadImages';
import Shopable from './Shopable';
import SubHeaderCrumbs from '../CommonComponents/SubHeader';
import { FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import { IS_IN_IFRAM, shopifyApp } from '../../../../utils';
import { GET_ADD_FEED } from '../../../../actions/moderationActions';
import { UPDATE_USER_HOME_DATA } from '../../../../actions/sceneActions';
import { updateSalesModal } from '../../../../actions/loginActions';
import AppLanguage from './AppLanguage';
import "./appsMore.scss"

class AppsMore extends Component {
    constructor(props) {
        super(props)
        this.state = { customeNetWorkList: [], filterToggle: false, currentTitle: 'Shoppable', activeKey: 'app_10' };
    }


    componentDidMount() {
        const { netWorkList, GET_ADD_FEED } = this.props
        if (!netWorkList?.length) GET_ADD_FEED()
    }

    closeAside = () => this.setState({ filterToggle: false })

    render() {
        const { filterToggle, currentTitle, activeKey } = this.state;
        const { allWalls, userData, UPDATE_USER_HOME_DATA, updateSalesModal } = this.props
        const isShopify = shopifyApp();
        const isIFrame = IS_IN_IFRAM();
        const content = window.APP_CONTENT['apps_more'] || {};
        const breadCrumbs = window?.APP_CONTENT?.['sideBar'] || {};

        return <div className={`d-flex flex-column flex-column-fluid full-content py-0 ${isShopify ? 'mt-5' : ''}`}>
            <SubHeaderCrumbs crumbs={[{ name: breadCrumbs?.appsMore, link: "/apps-more", icon: "grid-2" }, { name: breadCrumbs?.home, link: "/home" }, { name: currentTitle }]} crumbIcon={'grid-2'} />
            <Container className={`${!isShopify ? 'mw-100 px-0 container-apps-and-more' : ''}`}>
                <Tab.Container id="apps_more__" activeKey={activeKey} onSelect={event => this.setState({ activeKey: event })}>
                    <Card className="content_inner apps_more border-0 rounded-0 bg-transparent shadow-none">
                        <Card.Body className='p-0 d-flex w-100 '>
                            <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                <Card className='a_header_ rounded-0 rounded-0'>
                                    <Card.Header className='py-3 border-0'>
                                        <p className="h5 my-2 ">{content?.heading ?? "Apps & more"}</p>
                                    </Card.Header>
                                    <Card.Body className='aside_body p-0 w-100 border-0'>
                                        <div className='pb-3 mb-4'>
                                            <Nav variant="primary" className='flex-column'>
                                                <Row className="select-group d-flex m-0 p-2 pt-0">
                                                    {content?.features?.map((item, index) => (
                                                        (item?.id == 11 && isIFrame) ? null : <Col className={`col-4 px-1 mb-2`} key={index}>
                                                        <Nav.Item className='apps_tab h-100'>
                                                            <Nav.Link eventKey={`app_${item.id}`} variant="grid" data-grid={item.id} className={`border rounded d-flex flex-column align-items-center p-1 w-100`} onClick={(e) => this.setState({ currentTitle: item?.title ?? "" })}>
                                                                <div className="w-100 h-68px f-center mb-1">
                                                                    <i className={`fa-regular fa-${item.icon}`}></i>
                                                                </div>
                                                                <p className="mb-0 text-center fw-semibold fs-9 f-center">{item.title}</p>
                                                            </Nav.Link>
                                                        </Nav.Item>
                                                    </Col>
                                                    ))}
                                                </Row>
                                            </Nav>
                                        </div>
                                    </Card.Body>
                                </Card>
                            </CardAside>
                            <Card className={`${!isShopify ? "m-5" : "rounded-0 shadow-none"} w-100`}>
                                <div className="preview_ p_header_">
                                    <Card.Header className="py-4 f-between w-100 px-xl-7">
                                        <p className="h5 my-2 ">{currentTitle}</p>
                                        <div>
                                            <Button id="toggle_aside" variant='link' size='sm' className='d-flex d-xl-none' onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                                {content?.menu ?? "Menu"}
                                                <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                            </Button>
                                        </div>
                                    </Card.Header>
                                    <Card.Body className='p-0 px-xl-4 card-body-download'>
                                        <Tab.Content>
                                            {/* <Tab.Pane className="my-5" eventKey={`app_6`}>
                                                {activeKey == 'app_6' && <Language allWalls={allWalls} getNewData={() => UPDATE_USER_HOME_DATA()} />}
                                            </Tab.Pane> */}
                                            <Tab.Pane className="my-5" eventKey={`app_7`}>
                                                {activeKey == 'app_7' && <HashHighlighter allWalls={allWalls} getNewData={() => UPDATE_USER_HOME_DATA()} />}
                                            </Tab.Pane>
                                            <Tab.Pane className="my-5" eventKey={`app_8`}>
                                                {activeKey == 'app_8' && <Api userData={userData} updateSalesModal={updateSalesModal} />}
                                            </Tab.Pane>
                                            <Tab.Pane className="my-5" eventKey={`app_9`}>
                                                {activeKey == 'app_9' && <Download content={content?.download} userData={userData} allWalls={allWalls} />}
                                            </Tab.Pane>
                                            <Tab.Pane className="my-5" eventKey={`app_10`}>
                                                {activeKey == 'app_10' && <Shopable userData={userData} allWalls={allWalls} activeKey={activeKey} content={content?.shoppable} />}
                                            </Tab.Pane>
                                            <Tab.Pane className="my-5" eventKey={`app_11`}>
                                                {activeKey == 'app_11' && <AppLanguage userData={userData} />}
                                            </Tab.Pane>
                                        </Tab.Content>
                                    </Card.Body>
                                </div>
                            </Card>
                        </Card.Body>
                    </Card>
                </Tab.Container>
            </Container>
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data,
        netWorkList: state.addFeedReducer?.addFeedNetworks?.networks,
        allWalls: state.dashboardData?.dashboarddata?.allWalls
    }
}

const mapDispatchToProps = dispatch => {
    return {
        UPDATE_USER_HOME_DATA: () => dispatch(UPDATE_USER_HOME_DATA()),
        GET_ADD_FEED: () => dispatch(GET_ADD_FEED()),
        updateSalesModal: (data) => dispatch(updateSalesModal(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppsMore)