import React, { useCallback, useMemo, useState } from 'react';
import { Image, Button, Container, Tab, ToggleButtonGroup, ToggleButton, Card } from 'react-bootstrap';
import { MediaBaseUrl } from '../../../../../actions/api';
import { LANGUAGE_SUPPORTS } from '../../../../../actions/constants';
import { changeAppLanguage } from '../../../../../actions/appsMoreAction';

const AppLanguage = ({ userData }) => {
    const [chooseLang, setChooseLang] = useState(userData?.user?.langSelect ?? "en");
    const content = useMemo(() => window?.APP_CONTENT?.common ?? {}, [])
    const isDisable = useMemo(() => userData?.user?.langSelect == chooseLang, [chooseLang, userData])

    const onSubmitData = useCallback(() => changeAppLanguage(chooseLang), [chooseLang]);

    return (
        <Container fluid>
            <div className="my-5">
                <Tab.Container id="lang__" defaultActiveKey={"language"}>
                    <Tab.Content style={{ paddingBottom: "70px" }}>
                        <Tab.Pane eventKey="language">
                            <ToggleButtonGroup type="radio" name="lang" className='flex-wrap' defaultValue={chooseLang}>
                                {LANGUAGE_SUPPORTS?.map((item, index) => (
                                    <ToggleButton key={index} className={`mb-5 me-5 bg-transparent text-dark ${chooseLang == item.value ? '' : 'border'}`} value={item.value} onClick={() => setChooseLang(item.value)}>
                                        <Image className='me-2 border' src={`${MediaBaseUrl}media/images/flags/${item.value}.svg`} height={17} width={28} fluid />
                                        {item.label}
                                    </ToggleButton>
                                ))}
                            </ToggleButtonGroup>
                            <Card.Footer className="f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body">
                                <Button type="submit" variant='primary' className={`btn-min-w`} onClick={isDisable ? null : onSubmitData} disabled={isDisable}>{content?.save ?? ""}</Button>
                            </Card.Footer>
                        </Tab.Pane>
                    </Tab.Content>
                </Tab.Container>
            </div>
        </Container>
    )
}

export default AppLanguage