import React, { useEffect, useState } from 'react';
import Select from 'react-select';
import { GET_POST, HOST_URL, IMAGE_REQUEST_CREATE, IMAGE_REQUEST_LIST } from '../../../../../actions/api';
import { Container } from 'react-bootstrap';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';
import { useNavigate } from 'react-router-dom';
import { useApiRequest } from '../../../../../hooks/useApiRequest';
import Gallery from '../../EmailCampaign/Gallery';

export default function Download({ userData, allWalls, content }) {
    const [downloadImage, setDownloadImage] = useState(true);
    const [downloadRequests, setDownloadRequests] = useState([]);
    const [downloadWall, setDownloadWall] = useState([]);
    const [selectedWall, setSelectedWall] = useState({});
    const [posts, setPosts] = useState([]);
    const navigate = useNavigate();
    const { request: requestImage, spinner } = useApiRequest();
    const { request: getApiRequest } = useApiRequest();


    const onCustomPostSubmit = (e) => {
        e.preventDefault();
        requestImage({
            method: 'POST',
            url: IMAGE_REQUEST_CREATE,
            body: {
                ownerId: localStorage.getItem('ownerId'),
                wallId: selectedWall?.value
            },
            onSuccess: () => {
                setDownloadImage(false);
                getDownloadData();
            }
        });
    }


    const getDownloadData = () => {
        if (allWalls?.length > 0) {
            const walls = allWalls?.map((item) => ({
                label: item.name,
                value: item.id
            })) ?? [];
            const selWallId = walls?.find((item) => item.value == localStorage.getItem('wallId'));
            setDownloadWall(walls);
            setSelectedWall(selWallId);
            if (selWallId?.value) getPosts(selWallId);
        }

        getApiRequest({
            url: IMAGE_REQUEST_LIST,
            showToast: false,
            onSuccess: (data) => {
                const requests = data?.responseData || [];
                setDownloadImage(requests?.length == 0)
                if (requests?.length > 0) getPosts({ label: "", value: requests[0].wallId });
                setDownloadRequests(requests);
            }
        })
    }

    const getPosts = (wall) => {
        setSelectedWall(wall);
        getApiRequest({
            url: `${GET_POST}/${wall.value}`,
            showToast: false,
            onSuccess: (data) => {
                setPosts(data?.responseData || []);
            }
        })
    }

    useEffect(() => {
        if (userData?.UserRule?.download_img) getDownloadData();
    }, [userData]);

    return <Container fluid>
        {userData?.UserRule?.download_img ?
            <div>
                {!downloadImage ? <div className="row customPostSec postcWrapC">
                    <div className="col-md-12 wallcreateshow">
                        <div className="portlet-body">

                            <div className="d-flex justify-content-end">
                                <div onClick={(e) => setDownloadImage(true)} className="commanBtn btn-sm btn btn-primary dwnldImgsBtn mb-0">{content?.add_new ?? ""}</div>
                            </div>

                            <label className="columnTitle"></label>
                            <div className="table-scrollable table-scrollable-borderless globalTblList mt0">
                                {/* <table className="table table-hover"> */}
                                <table className="table">
                                    <tbody>
                                        <tr className="uppercase text-center">
                                            <th className="text-start">{content?.wall_name ?? ""}</th>
                                            <th className="text-start">{content?.status ?? ""}</th>
                                            <th className="text-start">{content?.expire_on ?? ""}</th>
                                            <th className="text-center">{content?.action ?? ""}</th>
                                        </tr>
                                    </tbody>
                                    <tbody>
                                        {downloadRequests.map((data, index) => {

                                            return (<tr className="text-center" align="center" key={index}>
                                                <td className="authorPicTd text-start align-middle"> {data.wall?.name || ''}</td>

                                                <td className="align-middle text-start">{data.download_complete == 1 ? <font color='green'>{content?.status_completed ?? ""}</font> : data.download_complete == 2 ? <font color="orange">{content?.no_data ?? ""}</font> : <font color="orange">{content?.in_progress ?? ""}</font>}</td>

                                                <td className="align-middle text-start">{data.download_complete == 1 ? data.download_cron_time : <font color="orange">-</font>}
                                                </td>

                                                <td className="align-middle text-center">
                                                    <a href={`${data.download_complete == 1 ? `${HOST_URL}/wallimages/archive/${data.foldername}.zip?v=${Math.floor(Math.random() * 100000)}` : "javascript:void(0)"}`}
                                                        className={`btn btn-default btn-sm tooltips ${data.download_complete == 1 ? 'readyDwnld' : null} `}>
                                                        <i className="fa fa-download me-0"></i>
                                                    </a>
                                                </td>
                                            </tr>)
                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div> : <div>
                    <form onSubmit={onCustomPostSubmit} className="maintainPopUp noLoader" method="post" accept-charset="utf-8">
                        <div className="downloadImgColm">
                            <div className="row customPostSec postcWrapC">
                                <div className="col-md-12 wallcreateshow">
                                    <div className="portlet-body">

                                        {/* {downloadRequests.length > 0 ?
                                        <ul className="nav justify-content-end">
                                            <li>
                                                <div onClick={(e) => setDownloadImage(false)} title="create" data-theme="dark" data-bs-toggle="tooltip" className="commanBtn btn btn-sm btn-primary popUp maintainPopUp creatNewPostBtn tooltips" data-popup-width="data-width='750px'">
                                                    <i className="fa fa-chevron-left"></i> Back
                                                </div>
                                            </li>
                                        </ul> : null} */}

                                        <div className="downldSlctWalBlock downloadImages_wrap">
                                            <div className="row">
                                                <div className="col-md-6 col-sm-6">
                                                    <div className="form-group">
                                                        <label htmlFor="" className="form-label">
                                                            {content?.select_wall ?? ""}
                                                        </label>
                                                        <div className="input-group flex-nowrap" style={{ maxWidth: '400px' }}>
                                                            {/* <div className="input-group-prepend">
                                                            <span className="input-group-text rounded-0 rounded-start h-100">
                                                                <i className="fa-duotone fa-solid fa-grid-2"></i>
                                                            </span>
                                                        </div> */}
                                                            <Select className="w-100" classNamePrefix="selectpicker " onChange={getPosts} options={downloadWall} value={selectedWall} />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-md-6 col-sm-6">
                                                    {downloadRequests.length > 0 ?
                                                        <ul className="nav justify-content-end">
                                                            <li>
                                                                <div onClick={(e) => setDownloadImage(false)} title="create" data-theme="dark" data-bs-toggle="tooltip" className="commanBtn btn btn-sm btn-primary popUp maintainPopUp creatNewPostBtn tooltips" data-popup-width="data-width='750px'">
                                                                    <i className="fa fa-chevron-left"></i> {content?.back ?? ""}
                                                                </div>
                                                            </li>
                                                        </ul> : null}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <div className="modal-footer pt-3"> */}
                        <div className="f-center justify-content-end position-absolute bottom-0 start-0 w-100 py-4 px-4 px-xl-6 bg-body card-footer z-1">
                            <button type="submit" className={`btn btn-primary ${spinner ? `spinner spinner-white spinner-right disabled` : null}`}>{content?.btn_download ?? ""}</button>
                        </div>
                    </form>
                    <React.Fragment key={selectedWall?.value}>
                        {posts?.length > 0 ?
                            <Gallery selectContent={posts} postContent={() => { }} />
                            : null
                            // <div className="h-100 f-center">
                            //     <EmptyWithIcon title={'Preview Missing? ðŸ‘€'} desc={'Please add post to download images.'} icon={'media'} button={{ action: () => {
                            //         localStorage.setItem('wallId', selectedWall?.value)
                            //         navigate(`/content/addfeed/${selectedWall?.value ?? ''}`);
                            //     }, text: 'Add Content' }} isFullScreen={false} />
                            // </div>
                        }
                    </React.Fragment>
                </div>}

            </div>
            :
            <div className='f-center' style={{ minHeight: 'calc(100vh - 300px)' }}>
                <EmptyWithIcon title={content?.title_upgrade ?? ""} desc={content?.dics_upgrade ?? ""} icon={'download'} button={{ action: () => navigate('/price'), text: content?.btn_upgrade ?? "" }} isFullScreen={false} />
            </div>
        }
    </Container>
}
