import { Container } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { getUserInfo } from '../../../../../actions/loginActions';
import { SHOPPABLE_TOGGLE_API } from '../../../../../actions/api';
import { useApiRequest } from '../../../../../hooks/useApiRequest';
import EmptyWithIcon from '../../CommonComponents/Empty/EmptyWithIcon';
import { PRODUCT_PLANS } from '../../../../../actions/constants';
import { useMemo } from 'react';
import { SWAL_FIRE } from '../../../../../utils';

export default function Shopable({ userData, allWalls, hideModal = null, activeKey, content }) {
    const { request: toogleShopable } = useApiRequest();
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const isShoppablePlan = useMemo(() => ["1", PRODUCT_PLANS.secondPaid, PRODUCT_PLANS.thirdPaid].includes(userData?.UserPlan?.planId ?? ""), [userData?.UserPlan?.planId]);

    const toggleAction = () => {
        if(!isShoppablePlan) navigate("/price");
        else{
            const status = userData?.UserRule?.shoppableStatus ?? 0;
            const { modal = {} } = content;
            if(status) {
                SWAL_FIRE({
                    title: modal?.title ?? "",
                    icon: "warning",
                    showCancelButton: true,
                    confirmButtonText: modal?.yes,
                    cancelButtonText: modal?.no,
                }).then((result) => {
                    if (result.value) getShoppable(status);
                });
            } else getShoppable(status);
        }
    }

    const getShoppable = (type) => {
        toogleShopable({
            method: 'POST',
            url: SHOPPABLE_TOGGLE_API,
            body: {
                shoppableStatus: type ? 0 : 1,
                wallId: allWalls?.map(wall=> wall.id) ?? []
            },
            onSuccess: () => {
                if(hideModal) hideModal();
                dispatch(getUserInfo(navigate, type ? "/home" : "/content/products"))
            }
        });
    }

    return <Container fluid>
        <div className='f-center' style={{ minHeight: 'calc(100vh - 300px)' }}>
            <EmptyWithIcon 
                title={content?.heading ?? "Inspire & Convert with UGC"} 
                desc={hideModal ? content?.sub_heading_2 ?? 'Turn your content into conversions. Enable Shoppable & tag products to drive sales directly from your UGC Gallery.' : content?.sub_heading ??'Embed shoppable galleries, run UGC-powered ads, and build custom experiences with our API.'} 
                icon={'shoppable'} 
                button={{ action: toggleAction, text: isShoppablePlan ? userData?.UserRule?.shoppableStatus ? content?.btn ?? 'Disable' : content?.btn_2 ?? 'Enable' : content?.btn_3 ?? 'Upgrade now' }} 
                isFullScreen={false}
                activeKey={activeKey}
            />
        </div>
    </Container>
}
